import { cryptoDecode } from "@/config/crypto.mjs";

export async function generateMetadata({ params }){
    const { id } = await params;
    let deCode = await cryptoDecode(decodeURIComponent(id));
    return {
        title: `MRA | ${deCode}`,
        description: "ระบบตรวจสอบคุณภาพเวชระเบียนผู้ป่วย | PDF",
    };
}
export default async function layout({children}) {
    return (
        <div>{children}</div>
    )
}
